.heo {
    height: 89vh;
    width: 100vw;
    background-image: linear-gradient(rgba(0,0,0,0.4), rgba(0,0,0,0.4)), url(./components/banner.jpg);
    background-position: center;
    background-size: cover;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items:center;
    overflow-y: hidden;
  }
  *{
    overflow-x: hidden;
  }
  .form-box {
    width: 400px;
    height: 50vh;
    position: relative;
    background: #fff;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    border-radius: 1rem;
  }
  .social-icons img{
    width: 30px;
    margin: 0 12px;
    box-shadow: 0 0 20px 0 #7f7f7f3d;
    cursor: pointer;
    border-radius: 50%;
    text-decoration: none;
}
  
  .input-field {
    width: 100%;
    padding: 10px 0;
    margin: 5px 0;
    border:0;
    border-bottom: 1px solid #999;
    outline: none;
    background: transparent;
  }
  
  .submit-btn {
    width: 85%;
    padding: 10px 20px;
    cursor: pointer;
    display: block;
    margin: auto;
    background: linear-gradient(to right, #11ff00, #ffad06);
    border: 0;
    outline: none;
    border-radius: 30px;
    margin-top: 15px;
  }
  
  
  span {
    color: #777;
    font-size: 12px;
  }

  .remember-password{
    display: flex;
    align-items: center;
    margin: 10px 0;
  }
  
  .error {
    color: red;
    margin-top: 10px;
    text-align: center;
  }
  
  @media (max-width: 768px){
    .form-box{
        width: 60vw;
    }
  }